/*  : redux , ,     */

import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import * as serviceWorkerRegistration from './serviceWorkerRegistration';
import { HashRouter as Router, Route, Routes } from 'react-router-dom';
import { Provider } from 'react-redux';
import { combineReducers, createStore } from 'redux';
import { HashRouter } from 'react-router-dom';

// Cart:    
let shoes = [
  {id: 0, name: "White and Black", quan: 0, size: 0},
  {id: 1, name: "Red Knit", quan: 0, size: 0},
];

function reducer(state = shoes, action){
  switch (action.type){

     // add:   , x o (findeIndex())
    case 'add':
      let found = state.findIndex((a) => {return a.id === action.data.id}); 
        /* state id: action.data  - ''  (found=0,1,2...) */
      if(found >= 0){
        let copy = [...state];
        copy[found].quan++;
        copy[found].size = action.data.size; /* size   */
        return copy
      } else { /* (=> -1)   */
        let copy = [...state];
        copy.push(action.data);
        return copy
      }
    
    // quanUp:  
    case 'quanUp':
      let found2 = state.findIndex((a) => {return a.id === action.data}); 
      if(found2 >= 0){
        let copy = [...state];
        copy[found2].quan++;
        return copy
      } 

    // quanDown:  
    case 'quanDown':
      let found3 = state.findIndex((a) => {return a.id === action.data}); 
      let copy=[...state];
      if(found3 >= 0 && copy[found3].quan > 0){ /*  0   */
        copy[found3].quan--;
        return copy
      } else {
        return copy
      }

    // deleteX:   
    case 'deleteX':
      let copy2 = [...state];
      let deleteV = copy2.filter((a)=>{
        return a.id !== action.data
      });
      return deleteV

    default:
      return state
  }
}


// Cart: alert UI
let alertTop = true;
function reducer2(state = alertTop, action){
  if (action.type === 'alertX'){
    return false
  } else {
    return state
  }
}


let store = createStore(combineReducers({reducer,reducer2}));

ReactDOM.render(
  <React.StrictMode>
    <HashRouter>
      <Provider store={store}>
        <App />
      </Provider>
    </HashRouter>
  </React.StrictMode>,
  document.getElementById('root')
);

serviceWorkerRegistration.register();

reportWebVitals();
